@keyframes fadeInDown {
    0% {
       opacity: 0;
       transform: translateY(-40px);
    }
    100% {
       opacity: 1;
       transform: translateY(0);
    }
 } 
 @keyframes fadeInLeft {
    0%{ opacity:0.2;
    transform: translateX(-800px);}
    100%{ opacity: 1;
    transform: translateX(0);}
  }

  @keyframes fadeInRight {
    0%{ opacity: 0.2;
    transform: translateX(800px);}
    100%{
      opacity: 1;
      transform: translateX(0);
    }
  }
 
