.hcls1{
 color: white;
font:bolder;

 font-size: 29px;
 padding-left: 70px;
 font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
 margin-top: 20px;
}
.hcls1 ul{
    display: flex;
}
.logo{
    color: rgb(70, 204, 70);
    margin: 0;
}

li{
    list-style: none;
    padding: 20px;
    
    font-style: none;
}
li a{
    text-decoration: none;
}

.hcls2{
    color: white;
    
    font-size: 20px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-top: 20px;
    margin-right: 200px;
    cursor: pointer;
    transition: transform(0.3s);
}
.hcls2 li:hover{
    
    transform:scale(1.13);
    
}
.list1{
    display: flex;
    
}
.headercls{
    display: flex;
    justify-content: space-between;
   height: 130px;
   
}




/*** RESPONSIVE FOR LAPTOPS ***/

/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {
    .hcls1{
        padding-left: 15px;
        font-size: 25px;

    }
    .headercls{
        
        display: flex;
        height: 90px;
    
    }
    .hcls2{
        font-size: 15px;
        margin-top: 33px;
        margin-right: 10px;
    }
    
    li{
        padding: 10px;
    }
    



}
/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
   
    .headercls{
        width: 100%;
        margin: 0 0rem;
    }
    .logo{
        padding-left: 0px;
    }
     
}
@media screen and (max-width: 576px) {
    .hcls1{
        padding-left: 0px;
        font-size: 15px;
        padding-top: 10px;
    }
    .logo{
        font-size: 16px;
        padding-right: 0px;
        padding-left: 0px;
    }
    .ve{
        font-size: 14px;
        padding-right: 0px;
    }
    .hcls2{
        padding-left: 0px;
        font-size: 12px;
        margin-right: 0px;
       
        margin-left: 0px;
    }
}
