@keyframes fadeInLeft {
    0%{ opacity:0.2;
    transform: translateX(-800px);}
    100%{ opacity: 1;
    transform: translateX(0);}
  }
  @keyframes fadeInRight {
    0%{ opacity: 0.2;
    transform: translateX(800px);}
    100%{
      opacity: 1;
      transform: translateX(0);
    }
  }
  @keyframes typing {
    from { width: 0 }
    
  }
  @keyframes cursor {
    50%{ border-color: transparent }
   
 }

 
