@keyframes fadeInLeft {
    0% {
        opacity: 0.2;
        transform: translateX(-800);
    }
    100%{
        opacity: 1;
        transform: translateX(0);
    }
    
}
.con1{
    margin: 0 8rem;
    animation: fadeInLeft 2s;
}
.con2{
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bolder;
    animation: fadeInLeft 2s;
}
.con3{
    color: rgb(31, 235, 31);
}
.con4{
    color: aliceblue;
}
.con5{
    color: aliceblue;
   
}
.con71{
    padding-bottom: 20px;
    padding-top: 10px;
}
.con0{
    background-color: #18182b;
    padding-top: 25px;
    
}
.con61{
    display: flex;
    padding-right: 40px;
   
    
    
    

}
.rammi{
    color: green;
}
.con7{
    color: violet;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}
.con6{
    margin: 0 8rem;
    animation: fadeInLeft 2s ;
    
    
}
.con8, .con9, .con10, .con11{
    font-size: 40px;
    padding-inline: 10px;
}
.con8:hover{
    color:orange;
    transform: scale(1.1);
}
.con9:hover{
    color:orange;
    transform: scale(1.1);
}
.con10:hover{
    color:orange;
    transform: scale(1.1);
}
.con11:hover{
    color:orange;
    transform: scale(1.1);
}


/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {

    .con1{
        margin: 0 5rem;
       
    }
    .con8, .con9, .con10, .con11{
        font-size: 30px;
        padding-inline: 8px;
        
    }
    .con6{
        margin: 0 5rem;
    }

    .con0{
        background-color: #18182b;
        padding-top: 10px;
        padding-bottom: 20px;
    }
    .con61{
        padding-top: 0;
        padding-bottom: 0px;
    }
    .con2{
        font-size: 25px;
        padding-bottom: 10px;
    }
    .con4{
        font-size: 20px;
    }
    .con5{
        font-size: 20px;
    }
    .con7{
        font-size: 20px;
        padding-bottom: 0px;
    }
}

