@keyframes fadeInDown {
    0%{opacity:0.2;
    transform: translateY(-80);}
    100%{
        opacity: 1;
        transform: translateY(0);
    } 
}

@keyframes fadeInLeft {
    0%{opacity:0.2;
    transform: translateX(-800);}
    100%{opacity: 1;
    transform: translateX(0);} 
}
.swiper1{
    
    margin-top:50px;
    
    
    
    
}
.swiper{
    width: 80%;
    animation: fadeInDown 2s;
    
}
.header{
    text-align: left;
    padding-left: 140px;
    padding-top: 30px;
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    animation: fadeInLeft 2s;
}
.sp5{
   color: rgb(69, 241, 69);
}
.swiper2{
    display: flex;
}
.div0{
    color: white;
   
    display: grid;
   justify-content: center;
    
   
    width: 100%;
    
}
.data{
    width: 80%;
    
    

}
.data h3{
    font-family: cursive;
    font-weight: lighter;
    color: burlywood;
}
.data h2{
    font-family:sans-serif;
    color: rgb(122, 233, 220)
    
}
.imgsrc{
    border: 10px solid #79c379;
    width: 80%;
}
.div1{
    height:100%;
    width: 100%;
    
}
.div1 img{
    height: 200px;
    width: 300px;
    
}

.swiper-button-next, .swiper-button-prev {
    /* top: var(--swiper-navigation-top-offset,70%);
    position: relative;
    top: -3rem; */
    opacity: 0;

   
}
.imgsrc{
    transition: transform(0.3s);
    transition-duration: 500ms;
    cursor: pointer;
}
.imgsrc:hover{
    transform: scale(1.05);
    box-shadow: 0px 0px 30px #747272;
}

/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {

    .header{
        font-size: 25px;
    }
    
    /* .swiper{
        
        width: 70%;
    } */
    .div1 img{
        height: 200px;
        width: 250px;
    }
    .data h2, .data h4{
        font-size: 15px;
    }
   
}
/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {

    .swiper1{
        height: 400px;
    }
    .imgsrc{
        border: 4px solid #79c379;
        width: 50%;
        

    }
    .div0{
        margin-left: 3.5rem;
        display: grid;
    }
    .div1 img{
        height: 200px;
        width: 200px;
    }
}
@media screen and (max-width: 576px) {
    .swiper1{
        margin: 0 2rem;
        width: 100%;
       
       
    }
    .imgsrc{
        height: 200px;
    }

}
/*** FOR SMALL DEVICES ***/
@media screen and (max-width: 350px) {
    .imgsrc{
        width: 150px;
        
    }
    .data{
        font-size: 20px;
    }
}