@keyframes fadeInDown {
    0% {
       opacity: 0;
       transform: translateY(-20px);
    }
    100% {
       opacity: 1;
       transform: translateY(0);
    }
 } 

 @keyframes fadeInLeft {
    0%{ opacity:0.2;
    transform: translateX(-800px);}
    100%{ opacity: 1;
    transform: translateX(0);}
  }

  @keyframes fadeInRight {
    0%{ opacity: 0.2;
    transform: translateX(800px);}
    100%{
      opacity: 1;
      transform: translateX(0);
    }
  }
  
.ser1{
  background-color: #18182b;
    height: 600px;


}
.ser2{
    font-size: 23px;
    scroll-padding-bottom: 30px;
    margin: 0 8rem;
    animation: fadeInLeft 2s ;
}
p{
    padding: 0 0 10px 0;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}


.sp1 , .sp2 ,.p1{
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 30px;
    font-weight: bold;
}
.ser3{
    width: 30px;
    display: flex;
    margin-left: 20rem;
    margin-right: 6rem;
    margin-bottom: 40px;
    align-items: center;
    cursor: pointer;
    animation: fadeInDown 2s;
    
    
}
.img3:hover , .img4:hover{
    transform: scale(1.05);
    

}
.img3{
    height: 250px;
    width: 400px;
    margin-right: 50px;
}
.img4{
    height: 250px;
    width: 400px;
}


/***FOR LARGE DEVICE ****/
@media screen and (max-width: 972px) {

    .ser2{
        font-size:22px ;
    }
    .p1{
        font-size: 17px;

    }
    .sp1, .sp2{
        font-size: 17px;
    }
    .img3{
        height: 150px;
        width: 300px;
        margin-right: 30px;
    }
    .ser3{
        width: 10px;
        margin-left: 2.5rem;
        margin-right: 10px;

    }
    .img4{
        height: 150px;
        width: 300px;
    }
    .ser1{
        height: 400px;
    }
}
/*** FOR MEDIUM DEVICES ***/
@media screen and (max-width: 768px) {
    .img3{
        width: 200px;
        margin-right: 0px;
    }
    .img4{
        width: 200px;
    }
    .ser3{
        margin: 0 3rem;

    }
    .ser1{
        height: 400px;
        width: 100%;
    }



}
@media screen and (max-width: 576px) {
    .ser1{
        width: 100%;
        margin: 0 2rem;
        height: 360px;
    }
    .img3{
        width: 150px;
        margin-right: 20px;
        height: 140px;
        
        
        
    }
    .img4{
        width: 150px;
        height: 140px;
        
    }
    .imgsrc{
        width: 200px;
        
    }
    .ser3{
        align-content: center;
        margin: 0 1.5rem;
        
    }
}